.services-container {
  padding: 40px;
  background: url('../assets/banner-image.jpg') no-repeat center center;
  background-size: cover;
  position: relative;
}

.services-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(10px);
  z-index: 1;
}

.service-category {
  position: relative;
  z-index: 2;
  margin-bottom: 40px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.2);
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.service-category h2 {
  color: #dc3545;
  font-size: 2.5rem;
  margin-bottom: 10px;
  grid-column: 1 / span 2;
  font-weight: bold;
  text-align: left;
}

.service-category ul {
  list-style-type: none;
  padding-left: 0;
  grid-column: 1;
}

.service-category li {
  font-size: 1.5rem;
  color: #333;
  margin: 5px 0;
  line-height: 1.5;
}

.service-category img {
  max-width: 100%;
  max-height: 250px;
  width: auto;
  height: auto;
  align-self: flex-end;
  justify-self: end;
  grid-column: 2;
}

/* Responsividade */
@media (max-width: 1024px) {
  .service-category {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .service-category h2,
  .service-category ul {
    grid-column: 1;
    text-align: center;
  }

  .service-category img {
    display: none;
  }
}

@media (max-width: 768px) {
  .service-category {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .service-category h2,
  .service-category ul {
    grid-column: 1;
    text-align: center;
  }

  .service-category img {
    display: none;
  }
}
